.input.radio {
  min-height: 13px;
  position: relative;
}
.input.radio input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  outline: 0;
  z-index: -1;
  width: 0;
  height: 0;
}
.input.radio input:checked + label::after {
  opacity: 1;
}
.input.radio label {
  cursor: pointer;
  user-select: none;
  padding-left: 20px;
}
.input.radio label::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border-width: 1px;
  border-radius: 50%;
}
.input.radio label::after {
  content: '';
  position: absolute;
  top: 4px;
  left: 4px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.2s ease-out;
}
.input.checkbox.toggle {
  min-height: 20px;
  position: relative;
}
.input.checkbox.toggle input {
  position: absolute;
  top: 0;
